import api from '../../utils/api.js';
import util from '../../utils/util.js';
const app = getApp();
export default {
	data() {
		return {
			grade: '',
			school: '',
			list: []
		};
	},
	onLoad() {
		uni.hideTabBar();
	},
	onShow() {
		const that = this;
		api.keeperIndex({
			success: function(res) {
				that.grade = res.grade;
				that.school = res.school;
				that.list = res.list;
			}
		});
	},
	methods: {
		navigator(index) {
			index = parseInt(index);
			let url = '';
			switch (index) {
				case 1:
					url = '../addCover/addCover';
					break;
				case 2:
					url = '../addCampus/addCampus';
					break;
				case 3:
					url = '../studentPhotoList/studentPhotoList';
					break;
				case 4:
					url = '../teacherPhotoList/teacherPhotoList';
					break;
				case 5:
					url = '../addClassroom/addClassroom';
					break;
				case 6:
					url = '../addHoliday/addHoliday';
					break;
				case 7:
					url = '../addOriginality/addOriginality';
					break;
				default:
					break;
			}
			uni.navigateTo({
				url: url
			});
		}
	}
};
